<template>
  <div>
    <i class="el-icon-question"></i>
    <svg>
      <use xlink:href="#quanxian"></use>
    </svg>
    <icon name="quanxian"></icon>
    <icon name="mima-"></icon>
    <div>
      <h3>填入数据显示el-icon图标</h3>
      <input v-model="iconname"/>
      <i :class="'el-icon-'+iconname"></i>
      <p>例如：date question delete</p>
    </div>
    <div>
      <h3>填入数据显示svg图标</h3>
      <input v-model="iconname1"/>
      <icon :name="iconname1"></icon>
      <p>例如：mima-  shangchuan- shezhi-</p>
    </div>
  </div>
</template>

<script>
import icon from '@/components/svg-icon'
// import '../../icons/svg/quanxian.svg'
export default {
  name: 'time',
  components: {
    icon
  },
  data () {
    return {
      iconname: 'date',
      iconname1: 'mima-'
    }
  }
}
</script>

<style scoped>

</style>



